
"use client";

import React, { createContext, useContext, useEffect, useState, useCallback } from 'react';
import { db } from '@/lib/firebase';
import { getInitialSemesters, getInitialStudents, getInitialRequests, getInitialUsers } from '@/lib/data';
import { doc, getDocs, collection, setDoc, writeBatch, updateDoc, addDoc, onSnapshot } from 'firebase/firestore';
import { Semester } from '@/lib/types';
import { useToast } from '@/hooks/use-toast';

interface DatabaseContextType {
  semesters: Semester[];
  loading: boolean;
  addSemester: (semesterData: Omit<Semester, 'id' | 'teachers' | 'masterSchedule' | 'weeklyAttendance'>) => Promise<boolean>;
  updateSemester: (semesterId: string, semesterData: Partial<Omit<Semester, 'id'>>) => Promise<boolean>;
}

const DatabaseContext = createContext<DatabaseContextType | undefined>(undefined);

async function seedInitialData() {
  try {
    const collectionsToSeed = [
      { name: "users", initialData: getInitialUsers, checkField: "username" },
      { name: "semesters", initialData: getInitialSemesters, checkField: "name" },
      { name: "students", initialData: getInitialStudents, checkField: "name" },
      { name: "requests", initialData: getInitialRequests, checkField: "type" }
    ];

    for (const { name, initialData } of collectionsToSeed) {
        const collRef = collection(db, name);
        const snapshot = await getDocs(collRef);
        if (snapshot.empty) {
            console.log(`Seeding ${name}...`);
            const data = initialData();
            for (const item of data) {
                // Ensure item has an id before trying to set it as a document name
                if (item.id) {
                    await setDoc(doc(db, name, item.id), item);
                } else {
                    console.warn(`Item in collection ${name} is missing an ID.`, item);
                }
            }
        }
    }
    console.log("Seeding check complete.");
  } catch (error) {
    console.error("Error seeding data: ", error);
  }
}


export function DatabaseProvider({ children }: { children: React.ReactNode }) {
  const [semesters, setSemesters] = useState<Semester[]>([]);
  const [loading, setLoading] = useState(true);
  const { toast } = useToast();

  useEffect(() => {
    seedInitialData();

    const unsubSemesters = onSnapshot(collection(db, 'semesters'), (snapshot) => {
        const allSemesters = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() } as Semester)).sort((a,b) => new Date(b.startDate).getTime() - new Date(a.startDate).getTime());
        setSemesters(allSemesters);
        setLoading(false);
    }, (error) => {
        console.error("Error fetching semesters: ", error);
        toast({ title: "Error", description: "Could not load semesters."});
        setLoading(false);
    });
    
    return () => unsubSemesters();

  }, [toast]);
  
  const addSemester = useCallback(async (semesterData: Omit<Semester, 'id' | 'teachers' | 'masterSchedule' | 'weeklyAttendance'>): Promise<boolean> => {
    try {
        const newSemesterRef = doc(collection(db, "semesters"));
        const newSemester: Semester = {
            ...semesterData,
            id: newSemesterRef.id,
            teachers: [],
            masterSchedule: {},
            weeklyAttendance: {}
        };
        await setDoc(newSemesterRef, newSemester);
        toast({ title: "Semester Added", description: `${semesterData.name} has been created.`});
        return true;
    } catch(error: any) {
        toast({ title: "Error", description: `Failed to add semester. ${error.message}`, variant: 'destructive'});
        return false;
    }
  }, [toast]);
  
  const updateSemester = useCallback(async (semesterId: string, semesterData: Partial<Omit<Semester, 'id'>>): Promise<boolean> => {
    const semesterRef = doc(db, 'semesters', semesterId);
    try {
        await updateDoc(semesterRef, semesterData);
        toast({ title: "Semester Updated", description: "Semester details have been saved." });
        return true;
    } catch (error: any) {
        toast({ title: "Error", description: `Failed to update semester. ${error.message}`, variant: "destructive" });
        return false;
    }
  }, [toast]);

  const value = { semesters, loading, addSemester, updateSemester };

  return <DatabaseContext.Provider value={value}>{children}</DatabaseContext.Provider>;
}

export function useDatabase() {
  const context = useContext(DatabaseContext);
  if (context === undefined) {
    throw new Error('useDatabase must be used within a DatabaseProvider');
  }
  return context;
}
